<template>
  <div class="rank limit">
    <div class="part">
      <h2 class="title">网易云听榜</h2>
      <song-sheet :songSheet="featureList"></song-sheet>
    </div>
    <div class="part ">
      <h2 class="title">全球媒体榜</h2>
      <song-sheet :songSheet="globalList"></song-sheet>
    </div>
    <loading v-show="isLoading"></loading>

  </div>
</template>

<script>
import songSheet from '@/components/common/songSheet/songSheet'
import { rankList } from '@/network/rank'
export default {
  components: { songSheet },
  data() {
    return {
      ranks: [],
     isLoading:true
    }
  },
  computed: {
    featureList() {
      return this.ranks.slice(0, 4)
    },
    globalList: function() {
      return this.ranks.slice(4, this.ranks.length)
    }
  },
  mounted() {
    rankList().then(res => {
      this.ranks = res.data.list
      console.log(this.ranks)
      this.isLoading=false
    })
  }
}
</script>

<style scoped lang="less">
.part {
  margin: 20px -0;
  font-size: 14px;
  font-weight: 400;
  .title {
      margin-left: 10px;
    border-left: 2px pink solid;
    padding-left: 15px;
  }
}
</style>
